*{
  font-family: 'Times New Roman', Times, serif;
}

body{
  padding: 60px;
}


input{
  color: gray;
  font-size: 1.2rem;
  width: 100%;
}

.card{
  align-content: center;
  border-radius: 6px;
  border: 1px solid gray;
  margin-bottom: 10px;
  margin-right: 10px;
  overflow: hidden;

}

.card p{
  text-align: center;
  padding: 5px;
}

.card img{
  max-height: 170px;
}

.card-grid{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}